import React from "react";
import ReactDOM from "react-dom";

class Formpage extends React.Component{
	constructor(){
		super()
		this.state={
			firstName:"",
			secName:"",
			email:"",
			isfriendly:false
		}
		this.handlechange=this.handlechange.bind(this)
	}
	
	handlechange(event){
		
		const{name,value,type,checked}=event.target
		type==="checkbox"?this.setState({ [name]:checked}):
		this.setState({	[name]:value})
	}
	render(){
		return(
			<form>
			<input name="firstName" onChange={this.handlechange}  type="text" placeholder="firstName" /><br/>
			<input onChange={this.handlechange} type="text" name="secName" placeholder="secName" /><br/>
			<input onChange={this.handlechange} type="text" name="email" placeholder="email" /><br/>
			<textarea value="address" name="address"/><br/>
			Is Friendly<input type="checkbox" onChange={this.handlechange}  name="isfriendly" checked={this.state.isfriendly} /><br/>
			
			<button  name="submit">Submit</button><br/>
			<label>{this.state.firstName}</label><br/>
			<label>{this.state.secName}</label><br/>
			<label>{this.state.email}</label>
			</form>
		)
	}
}
export default Formpage